import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import articles from './article-content';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';

// const ArticlePage = ({ match }) => {
    // const { name } = match.params;
    
    const ArticlePage = () => {
        const [articleInfo, setArticleInfo] = useState({upvotes:0, comments:[]});
        const { name } = useParams()  // React Hooks
    
        useEffect(() => {
            const fetchArticleInfo = async () => {
                const result = await fetch(`/api/articles/${name}`);
                const body = await result.json();
                setArticleInfo(body);
            }

            fetchArticleInfo();
        }, [name]);

        const matchingArticle = articles.find(
            article => article.name === name
        );

        return !matchingArticle ? <h1> Article doesn't exist!</h1> : (
        <>
            <h1>{matchingArticle.title}</h1>
            <UpvotesSection 
                upvotes={articleInfo.upvotes}
                articleName={matchingArticle.name}
                setArticleInfo={setArticleInfo}
            />
            {matchingArticle.content.map((paragraph, index) => (
                <p key={index}>{paragraph}</p>
            ))}

            <CommentsList comments={articleInfo.comments} />
        </>
    );
}

export default ArticlePage;